$asset-root-path: '..' !default;

@import "base/reset";
@import "base/typography";
@import "utils/colors/theme";
@import "utils/colors/layers";
@import "utils/colors/classes";
@import "layout/container";
@import "layout/boxes";
@import "layout/margins";
@import "components/footer";
@import "components/collections";
@import "components/inputs";
@import "components/links";
@import "components/logos";
@import "components/tables";
@import "components/errorbox";
@import "components/breadcrumb";
@import "components/loading";
@import "components/materialicons";
@import "components/flowcanvas";

html {
	height: 100%;
}

body {

	&.theme-wireworks {
		--color-primary: #{$wireworks-color-primary};
		--color-primary-light: #{$wireworks-color-primary-light};
		--color-primary-extralight: #{$wireworks-color-primary-extralight};
		--color-secondary: #{$wireworks-color-secondary};
		--color-secondary-light: #{$wireworks-color-secondary-light};
	}

	&.theme-layer1 {
		--color-primary: #{$layer1-color-primary};
		--color-primary-light: #{$layer1-color-primary-light};
		--color-primary-extralight: #{$layer1-color-primary-extralight};
		--color-secondary: #{$layer1-color-secondary};
		--color-secondary-light: #{$layer1-color-secondary-light};
	}

	&.theme-layer2 {
		--color-primary: #{$layer2-color-primary};
		--color-primary-light: #{$layer2-color-primary-light};
		--color-primary-extralight: #{$layer2-color-primary-extralight};
		--color-secondary: #{$layer2-color-secondary};
		--color-secondary-light: #{$layer2-color-secondary-light};
	}

	&.theme-layer3 {
		--color-primary: #{$layer3-color-primary};
		--color-primary-light: #{$layer3-color-primary-light};
		--color-primary-extralight: #{$layer3-color-primary-extralight};
		--color-secondary: #{$layer3-color-secondary};
		--color-secondary-light: #{$layer3-color-secondary-light};
	}

	&.theme-layer4 {
		--color-primary: #{$layer4-color-primary};
		--color-primary-light: #{$layer4-color-primary-light};
		--color-primary-extralight: #{$layer4-color-primary-extralight};
		--color-secondary: #{$layer4-color-secondary};
		--color-secondary-light: #{$layer4-color-secondary-light};
	}

	&.theme-layer5 {
		--color-primary: #{$layer5-color-primary};
		--color-primary-light: #{$layer5-color-primary-light};
		--color-primary-extralight: #{$layer5-color-primary-extralight};
		--color-secondary: #{$layer5-color-secondary};
		--color-secondary-light: #{$layer5-color-secondary-light};
	}

	font-family: $font-family-base;
	background: theme(primary);
	height: 100%;
	transition: background-color 1s cubic-bezier(0.645, 0.045, 0.355, 1);

}